.grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 10px;
	@media (max-width: 576px) {
		gap: 3px;
	}

	& li {
		overflow: hidden;
		aspect-ratio: 1;
	}
	& img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	& img:hover {
		filter: brightness(0.7);
	}
}
